a {
    font-size: 15px;
   
}

.footer-border{
    background-color: #232323;
    padding:20px;
    color: #bdbfae;
}

p.thicker {
    font-weight: lighter;
  }

p.bold{
    font-weight: bolder;
    font-size: 25px;
}

.footer-box{
    padding:25px;
}

.header {
    font-size: 20px;
}

.copy-right{
    background-color: #5c5c58;
    font-size: 12px;
    color: dbdbd7;
    line-height: 30px;
    text-align: left;
    padding: 5px;
}
p.copy-right {
    margin-bottom: 0px;
}

.top-header{
    background-color: #1e468b;
    padding: 10px;
    line-height: 40px;
    text-align: right;
    font-size: 30px;
    
}

.top-header > div {
    margin:5px;
    display: inline-block;
}

.tel-number{
   
    font-weight: bold;
    color: white;
    
}

.menu-dropdown {
    font-size: 40px;
    line-height: 64px;
    cursor: pointer;
}

.menu-list > * {
    margin: 10px;
    border: solid black 1px;
    border-radius: 15px;
    margin-inline-start: 0%;
    font-size: 15px;
    line-height: 25px;
}

@media only screen and (max-width: 1150px) {
    .menu-bar {
      display: none;
    }
  }

  @media only screen and (min-width: 1150px) {
    .menu-dropdown {
      display: none;
    
    }
  }